
.about_img {
    width: 320px;
    border-radius: .5rem;
    justify-self: center;
    align-self: center;
}

.about_description{
    text-align: center;
    margin-bottom: var(--mb-2-5);
}

.about_info{
    display: flex;
    justify-content: space-evenly;
    margin-bottom: var(--mb-2-5);
}

.about_info_title{
    font-size: var(--h2-font-size);
    font-weight: var(--font-semi-bold);
    color: var(--title-color);
}

.about_info_name{
    font-size: var(--smaller-font-size);

}

.about_info_title, .about_info_name{
display: block;
text-align: center;
}

.about_buttons{
    display: flex;
    justify-content: center;
}